import React from 'react';
import styled from 'styled-components';

const StyledStats = styled.ul`
	max-width: 900px;
	margin: 0 auto;
	display: flex;
	list-style: none;
	li {
		padding: 0 2rem;
	}
`;

const UserStats = props => (
	<div>
		<StyledStats>
			<li>{props.itemsCount}</li>
			<li>{props.me.name}</li>
			<li>{props.me.email}</li>
		</StyledStats>
	</div>
);

export default UserStats;
